/* global document, Office, Word */

let currentPath = []; // Represents the path to the current folder, e.g., ["folder1", "subfolderA"]

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("save-snippet").onclick = saveSnippet;
    document.getElementById("create-folder").onclick = createFolder;

    document.getElementById("snippet-name").addEventListener("keydown", (event) => {
        if (event.key === "Enter") {
            saveSnippet();
        }
    });

    document.getElementById("folder-name").addEventListener("keydown", (event) => {
        if (event.key === "Enter") {
            createFolder();
        }
    });

    initializeFileSystem();
    render();
  }
});

// --- File System Core ---

function initializeFileSystem() {
  if (!localStorage.getItem("snippetFileSystem")) {
    localStorage.setItem("snippetFileSystem", JSON.stringify({ type: "folder", children: {} }));
  }
}

function getFileSystem() {
  return JSON.parse(localStorage.getItem("snippetFileSystem"));
}

function saveFileSystem(fs) {
  localStorage.setItem("snippetFileSystem", JSON.stringify(fs));
}

// --- Actions ---

async function saveSnippet() {
  const nameInput = document.getElementById("snippet-name");
  const snippetName = nameInput.value.trim();
  if (!snippetName) {
    console.error("Snippet name cannot be empty.");
    return;
  }

  await Word.run(async (context) => {
    const range = context.document.getSelection();
    const ooxml = range.getOoxml();
    await context.sync();

    const fs = getFileSystem();
    let currentFolder = fs;
    for (const part of currentPath) {
      currentFolder = currentFolder.children[part];
    }

    if (currentFolder.children[snippetName]) {
      console.error("A snippet or folder with this name already exists.");
      return;
    }

    currentFolder.children[snippetName] = {
      type: "snippet",
      ooxml: ooxml.value,
    };

    saveFileSystem(fs);
    nameInput.value = "";
    render();
  });
}

function createFolder() {
  const nameInput = document.getElementById("folder-name");
  const folderName = nameInput.value.trim();
  if (!folderName) {
    console.error("Folder name cannot be empty.");
    return;
  }

  const fs = getFileSystem();
  let currentFolder = fs;
  for (const part of currentPath) {
    currentFolder = currentFolder.children[part];
  }

  if (currentFolder.children[folderName]) {
    console.error("A snippet or folder with this name already exists.");
    return;
  }

  currentFolder.children[folderName] = {
    type: "folder",
    children: {},
  };

  saveFileSystem(fs);
  nameInput.value = "";
  render();
}

async function pasteSnippet(name) {
  const fs = getFileSystem();
  let currentFolder = fs;
  for (const part of currentPath) {
    currentFolder = currentFolder.children[part];
  }
  const snippet = currentFolder.children[name];

  if (snippet && snippet.type === "snippet") {
    await Word.run(async (context) => {
      context.document.body.insertOoxml(snippet.ooxml, Word.InsertLocation.end);
      await context.sync();
    });
  }
}

// --- Rendering ---

function render() {
  renderBreadcrumb();
  renderItems();
}

function renderBreadcrumb() {
  const breadcrumbContainer = document.getElementById("breadcrumb");
  breadcrumbContainer.innerHTML = "";
  const rootLink = document.createElement("a");
  rootLink.href = "#";
  rootLink.innerText = "Root";
  rootLink.onclick = (e) => {
    e.preventDefault();
    currentPath = [];
    render();
  };
  breadcrumbContainer.appendChild(rootLink);

  let pathAccumulator = [];
  currentPath.forEach((part) => {
    pathAccumulator.push(part);
    const currentPartPath = [...pathAccumulator];
    const separator = document.createElement("span");
    separator.innerText = " > ";
    breadcrumbContainer.appendChild(separator);

    const partLink = document.createElement("a");
    partLink.href = "#";
    partLink.innerText = part;
    partLink.onclick = (e) => {
      e.preventDefault();
      currentPath = currentPartPath;
      render();
    };
    breadcrumbContainer.appendChild(partLink);
  });
}

function renderItems() {
  const listContainer = document.getElementById("snippet-list");
  listContainer.innerHTML = "";
  const fs = getFileSystem();
  let currentFolder = fs;
  for (const part of currentPath) {
    currentFolder = currentFolder.children[part];
  }

  const items = Object.keys(currentFolder.children).sort((a, b) => {
    const itemA = currentFolder.children[a];
    const itemB = currentFolder.children[b];
    if (itemA.type === "folder" && itemB.type !== "folder") return -1;
    if (itemA.type !== "folder" && itemB.type === "folder") return 1;
    return a.localeCompare(b);
  });

  items.forEach((name) => {
    const item = currentFolder.children[name];
    const listItem = document.createElement("li");
    listItem.className = "ms-ListItem";

    if (item.type === "folder") {
      listItem.innerHTML = `
        <i class="ms-Icon ms-Icon--FabricFolder"></i>
        <span class="ms-ListItem-primaryText">${name}</span>
      `;
      listItem.onclick = () => {
        currentPath.push(name);
        render();
      };
    } else {
      listItem.innerHTML = `
        <i class="ms-Icon ms-Icon--Page"></i>
        <span class="ms-ListItem-primaryText">${name}</span>
        <div class="ms-ListItem-actions">
          <button class="ms-Button paste-button" title="Paste Snippet"><i class="ms-Icon ms-Icon--Paste"></i></button>
        </div>
      `;
      listItem.querySelector(".paste-button").onclick = (e) => {
        e.stopPropagation();
        pasteSnippet(name);
      };
    }
    listContainer.appendChild(listItem);
  });
}
